<template>
    <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Rental Rate
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Rental Rate</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add Rental Rate</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  
                  <div class="form-group">
                    <label for="exampleInputPassword1">Service City</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedcity" v-autofocus required>
                    <option value="">Select City</option>
                    <option v-for="dept in listSCity" v-bind:value="dept.ServiceArea">
                      {{dept.ServiceArea}}
                      </option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Category Vehicle</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedvehicle" required>
                    <option value="">Select Category Vehicle</option>
                    <option v-for="dept in listCity" v-bind:value="dept.Category">
                      {{dept.Category}}
                      </option>
                    </select>                    
                  </div>
                  <div class="row">
                    <div class="col-md-6">
                    <label for="exampleInputEmail1">Hours</label>
                    <span><input type="number" class="form-control" id="exampleInputEmail1" v-model="hours1" placeholder="Enter Hours"></span></div>
                  <div class="col-md-6">
                    <label for="exampleInputEmail1">KMs</label>
                    <span><input type="number" class="form-control" id="exampleInputEmail1" v-model="kms1" placeholder="Enter KMs"></span>
                    </div>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Rental Price</label>                    
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Rental Price" v-model="rentalprice" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Maximum Price</label>                    
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Maximum Price" v-model="donkeyprice" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Display Price</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Display Price" v-model="offerprice" required>
                  </div>
                  <div class="row">
                    <div class="col-md-12"><h4>Grace Time</h4></div>
                    <div class="col-md-4">
                    <label for="exampleInputEmail1">Minutes</label>
                    <span><input type="number" class="form-control" id="exampleInputEmail1" v-model="hours2" placeholder="Enter Minutes"></span></div>
                  <div class="col-md-4">
                    <label for="exampleInputEmail1">KMs</label>
                    <span><input type="number" class="form-control" id="exampleInputEmail1" v-model="kms2" placeholder="Enter KMs"></span>
                    </div>
                    <div class="col-md-4">
                    <label for="exampleInputEmail1">Price</label>
                    <span><input type="text" class="form-control" id="exampleInputEmail1" v-model="price" placeholder="Enter Price"></span>
                    </div>
                  </div>
                </div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'RentalRateadd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {}, id: '', selectedvehicle: '', selectedcity: '', hours1: '', kms1: '', donkeyprice: '', offerprice: '', listCity: [], hours2: '', kms2: '', price: '', rentalprice: '', listSCity: []
    }
  },
  created: function () { this.autoInc() },
  methods: {
    autoInc () {
      let uris = this.$hostName + '/common/CategoryMaster'
      this.axios.get(uris).then((response) => {
        this.listCity = response.data
      })
      let uris1 = this.$hostName + '/common/ServiceArea'
      this.axios.get(uris1).then((response) => {
        this.listSCity = response.data
      })

      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/rental/RentalRate/Auto'
        this.axios.get(uri).then((response) => {
          console.log(response.data[0])
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/rental/RentalRate/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
                    // this.item = response.data;
          this.id = response.data[0]['id']
          this.item.id = this.id
          this.selectedvehicle = response.data[0]['CategoryVehicle']
          this.hours1 = response.data[0]['Hours1']
          this.kms1 = response.data[0]['KMs1']
          this.rentalprice = response.data[0]['rentalprice']
          this.donkeyprice = response.data[0]['donkeyprice']
          this.offerprice = response.data[0]['offerprice']
          this.hours2 = response.data[0]['Hours2']
          this.kms2 = response.data[0]['KMs2']
          this.price = response.data[0]['price']
          this.selectedcity = response.data[0]['ServiceCity']
        })
      }
    },
    addItem () {
      let uri = this.$hostName + '/rental/RentalRate/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/rental/RentalRate/update' }
      this.item.id = this.id
      this.item.selectedvehicle = this.selectedvehicle
      this.item.hours1 = this.hours1
      this.item.kms1 = this.kms1
      this.item.rentalprice = this.rentalprice
      this.item.donkeyprice = this.donkeyprice
      this.item.offerprice = this.offerprice
      this.item.hours2 = this.hours2
      this.item.kms2 = this.kms2
      this.item.price = this.price
      this.item.selectedcity = this.selectedcity

      this.axios.post(uri, this.item).then((response) => { this.$router.push({name: 'RentalRatevw'}) })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



