<template>
<div class="Admin-view">

   <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Offers & Updates
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Offers & Updates</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add Offers & Updates</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post" enctype="multipart/form-data">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Service City</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedcity" v-autofocus required>
                    <option value="">Select Service City</option>
                    <option v-for="dept in listCity" v-bind:value="dept.ServiceArea">
                      {{dept.ServiceArea}}
                      </option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Type</label>
                    <select class="form-control select2" style="width: 100%;" @change="onChange" v-model="selectedType" required>
                    <option selected="selected" value="Offers">Offers</option>
                    <option value="Updates">Updates</option>
                    </select>                    
                  </div>
                  <div class="form-group" v-if="offer1">
                    <div class="row">
                    <div class="col-md-6"><label for="exampleInputPassword1">Offer Code</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Offer Code" v-model="offercode">
                  </div>
                  <div class="col-md-6"><label for="exampleInputPassword1">Offer Rate</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Offer Rate" v-model="offerrate" >
                  </div>
                </div>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Content</label>
                    <textarea class="form-control" id="exampleInputPassword1" placeholder="Type Here..." v-model="note" required></textarea>
                  </div>
                    <div class="form-group row">
                      <div  class="col-md-6">
                    <label for="exampleInputPassword1">Image Upload</label>
                    <input type="file" accept="image/*" class="form-control" id="exampleInputPassword1" @change="imageChanged" name="files">
                    <div class="col-md-12" style="padding-top: 1em">
                    <progress :value.prop="uploadPercentage" max="100" v-if="isProgress"></progress>
                    </div>
                    </div>
                    <div class="col-md-6">                    
                    <img :src="product.image" width="230" height="180">
                    <!-- <img :src="product.image" width="230" height="180"> -->
                  </div>
                </div>
                </div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'CommonOffersadd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {}, id: '', listCity: [], selectedcity: '', offer1: true, selectedType: 'Offers', product: { image: '' }, offercode: '', offerrate: '', note: '', uploadPercentage: 0, isProgress: false, frmdata: ''
    }
  },
  created: function () { this.autoInc() },
  methods: {
    imageChanged (e) {
      console.log(e.target.files[0])
      this.product.files = e.target.files[0]
      var fileReader = new FileReader()

      fileReader.readAsDataURL(e.target.files[0])
      fileReader.onload = (e) => {
        this.product.image = e.target.result
      }
    },
    onChange: function () {
      if (this.selectedType === 'Offers') { this.offer1 = true } else { this.offer1 = false }
      return
    },
    autoInc () {
      let uris = this.$hostName + '/common/ServiceArea'
      this.axios.get(uris).then((response) => {
        this.listCity = response.data
      })
      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/advanced/CommonOffers/Auto'
        this.axios.get(uri).then((response) => {
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/advanced/CommonOffers/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
                    // this.item = response.data;
          this.id = response.data[0]['id']
          this.item.id = this.id
          this.selectedcity = response.data[0]['ServiceCity']
          this.selectedType = response.data[0]['Type']
          this.offer1 = false
          if (response.data[0]['Type'] === 'Offers') { this.offer1 = true }
          this.offercode = response.data[0]['OfferCode']
          this.offerrate = response.data[0]['OfferRate']
          this.note = response.data[0]['Content']
          this.product.image = response.data[0]['filePath']
          imageChanged()
          // console.log(this.product.image)
        })
      }
      this.$refs.search.focus()
    },
    addItem () {
      let data = new FormData()
      let file = this.product.files
      data.append('file', file)
      this.isProgress = true
      let uri = this.$hostName + '/advanced/CommonOffers/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/advanced/CommonOffers/update' }
      // this.item.id = this.id
      // this.item.selectedcity = this.selectedcity
      // this.item.selectedType = this.selectedType
      // this.item.offercode = this.offercode
      // this.item.offerrate = this.offerrate
      // this.item.note = this.note
      // this.item.image = this.product.image
      data.append('id', this.id)
      data.append('selectedcity', this.selectedcity)
      data.append('selectedType', this.selectedType)
      data.append('offercode', this.offercode)
      data.append('offerrate', this.offerrate)
      data.append('note', this.note)
      this.axios.post(uri, data, {onUploadProgress: function (progressEvent) {
        this.uploadPercentage = parseInt(Math.round((progressEvent.loaded * 100) / progressEvent.total))
      }.bind(this) }).then((response) => { this.$router.push({name: 'CommonOffersvw'}) })
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



