<template>
  <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Service Area
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Service Area</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

      <!-- Content Body-->

      <div class="col-md-12">
        <div class="card">
          <div class="card-header">
            <div class="row">
                  <div class="col-sm-6"><h4 class="text-left">Search : <input type="text" name="" v-model="search" placeholder="Type Here..."></h4></div>
                  <div class="col-sm-6"><h3 class="card-title text-right">
              <router-link :to="{ name: 'ServiceArea1add' }" class="btn btn-primary">Add Service Area</router-link></h3></div>
              </div>
    
      <!-- End Pagination Component -->
          </div>
          <!-- /.card-header -->
          <div class="card-body p-0">
            <table class="table table-condensed">
              <tr>
                <th style="width: 5%">#</th>
                <th style="width: 25%">Service City</th>
                <th style="width: 25%">Service Area</th>
                <th style="width: 15%">Latitude</th>
                <th style="width: 15%;">Longitude</th>
                <th style="width: 20%;">Radius</th>
                <th style="width: 5%;">Action</th>
              </tr>
              <tbody>
                <tr v-for="(item,index) in filteredCustomers">
                  <td>{{ item.id }}</td>
                  <td>{{ item.ServiceCity }}</td>
                  <td class="CellWithComment">{{ item.ServiceArea }}
                    <span class="CellComment">{{ item.Notes }}</span>
                  </td>
                  <td>{{ item.Latitude }}</td>
                  <td>{{ item.Longitude }}</td>
                  <td>{{ item.Radius }}</td>
                  <td>
                    <router-link :to="{name: 'ServiceArea1add',params:{id: item.id}}" class="btn btn-primary">Edit</router-link>
                  </td>
                  <td>
                    <button class="btn btn-danger" v-on:click="deleteItem(item.id,index)">Delete</button>
                  </td>
                </tr>

              </tbody>

            </table>
          </div>
          <!-- /.card-body -->
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  import Pagination from 'Pagination.vue'
  import axios from 'axios'

  let pagination = {
    current: 1,       // Current page
    total: 0,         // Items total count
    itemsPerPage: 2   // Items per page
}

  export default {
    name: 'ProductTypevw',
    data () {
      return { items: [],
        keys: [],
        search: '',
        pagination: pagination,
        error: '' }
    },
    components: { Pagination },
    computed:
    {
      filteredCustomers: function () {
        var self = this
        return this.items.filter(function (cust) { return cust.ServiceArea.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 || cust.ServiceCity.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 })
       // return this.customers;
      }
    },
    created: function () {
      let uri = this.$hostName + '/sharing/ServiceArea'
      axios.get(uri)
    .then(response => {
      // this.keys = Object.keys(response.data.results[0])
      this.items = response.data
      console.log(response.data.length)
      // Set pagination config based on response
      this.pagination.total = response.data.length
    })
    .catch(error => {
      this.error = error
    })
    },
    methods: {
      onChange (page) {
        console.log(`Getting page ${page}`)
        let uri = this.$hostName + '/sharing/ServiceArea'
        axios.get(uri)
      .then(response => {
        this.items = response.data
        this.pagination.current = page
      })
      .catch(error => {
        this.error = error
      })
      },
  
      FetchItem () {
        let uri = this.$hostName + '/sharing/ServiceArea'
        this.axios.get(uri).then((response) => {
          this.items = response.data
        })
      },
      deleteItem (id1, ri) {
        var r = confirm('Are u want to Delete?')
        if (r === true) {
          let uri = this.$hostName + '/sharing/ServiceArea/Del/' + id1
          this.items.splice(ri, 1)
          this.axios.get(uri).then((response) => {
  // console.log(response.data);
            this.$router.push({name: 'ServiceArea1vw'})
          })
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped="">
#MyTable{
  border-style:solid;
  border-color:black;
  border-width:2px
}

#MyTable td{
  border-style:solid;
  border-color:black;
  border-width:1px;
  padding:3px;
}

.CellWithComment{
  position:relative;
}

.CellComment{
  display:none;
  position:absolute; 
  z-index:100;
  border:1px;
  background-color:white;
  border-style:solid;
  border-width:1px;
  border-color:red;
  padding:3px;
  color:red; 
  top:30px; 
  left:20px;
}

.CellWithComment:hover span.CellComment{
  display:block;
}
</style>




