<template>
  <div id="app">
    <div class="wrapper">
      <va-navibar v-show="hidevw"></va-navibar>
      <va-slider :slideMenuItems="slideMenuItems" v-show="hidevw"></va-slider>
      <va-content-wrap v-show="hidevw"></va-content-wrap>
      <Modal></Modal>
    </div>
  </div>
</template>

<script>
import VANaviBar from 'NaviBar.vue'
import VASlider from 'Slider.vue'
import VAContentWrap from 'ContentWrap.vue'
import Modal from './components/Modal.vue'
import store from './vuex/store.js'
import slideMenuItems from './lib/slideMenuItems.js'

export default {
  name: 'app',
  data () {
    return {
      slideMenuItems: slideMenuItems,
      hidevw: true
    }
  },
  created () {
    if (window.location.pathname) {
      let url = '/admin'
      if (window.location.pathname === url) {
        this.hidevw = false
      }
    }
  },
  components: {
    'va-navibar': VANaviBar,
    'va-slider': VASlider,
    'va-content-wrap': VAContentWrap,
    Modal
  },
  store
}
</script>

<style>

</style>



