<template>
    <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        F.A.Q
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">FAQ</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add FAQ</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Question Body</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Question" v-model="ques" v-autofocus ref="focus" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Question link</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Example: http://google.com" v-model="link" ref="focus" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Description</label>
                    <textarea class="form-control" id="exampleInputPassword1" placeholder="Type Here..." v-model="desc" required></textarea>
                  </div>
                  <div class="form-group row">
                    <div class="col-md-4"><label for="exampleInputPassword1">Choose Services</label></div>
                    <div class="col-md-3"><input type="checkbox"  value="User" v-model="cuser">&nbsp;User</div>                    
                    <div class="col-md-3"><input type="checkbox"  value="Driver" v-model="cdriver">&nbsp;Driver</div>
                  </div>
                </div>
                <!-- /.card-body -->
                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'FAQadd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {}, id: '', ques: '', link: '', desc: '', cuser: '', cdriver: ''
    }
  },
  created: function () { this.autoInc() },
  methods: {
    autoInc () {
      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/advanced/FAQ/Auto'
        this.axios.get(uri).then((response) => {
          console.log(response.data[0])
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/advanced/FAQ/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
                    // this.item = response.data;
          this.id = response.data[0]['id']
          this.item.id = this.id
          this.ques = response.data[0]['QuestionBody']
          this.link = response.data[0]['QuestionLink']
          this.desc = response.data[0]['Description']
          this.cuser = false
          this.cdriver = false
          if (response.data[0]['isUser'] === 'true') { this.cuser = true }
          if (response.data[0]['isDriver'] === 'true') { this.cdriver = true }
        })
      }
    },
    addItem () {
      let uri = this.$hostName + '/advanced/FAQ/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/advanced/FAQ/update' }
      this.item.id = this.id
      this.item.ques = this.ques
      this.item.link = this.link
      this.item.desc = this.desc
      var cuser1 = 'false'
      var cdriver1 = 'false'
      if (this.cuser === true) cuser1 = 'true'
      if (this.cdriver === true) cdriver1 = 'true'
      this.item.cuser = cuser1
      this.item.cdriver = cdriver1

      this.axios.post(uri, this.item).then((response) => { this.$router.push({name: 'FAQvw'}) })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



