<template>
    <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Parcel Rate
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Parcel Rate</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add Parcel Rate</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Parcel</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedparcel" v-autofocus required>
                    <option value="">Select Parcel</option>
                    <option v-for="dept in listParcel" v-bind:value="dept.ParcelName">
                      {{dept.ParcelName}}
                      </option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Parcel Size</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedsize">
                    <option selected="selected" value="S-Small">S-Small</option>
                    <option value="M-Medium">M-Medium</option>
                    <option value="L-Large">L-Large</option>
                    <option value="XL-ExtraLarge">XL-ExtraLarge</option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">From City</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedfrom" required>
                    <option value="">Select City</option>
                    <option v-for="dept in listCity" v-bind:value="dept.City">
                      {{dept.City}}
                      </option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">To City</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedto" required>
                    <option value="">Select City</option>
                    <option v-for="dept in listCity" v-bind:value="dept.City">
                      {{dept.City}}
                      </option>
                    </select>                    
                  </div>
                  
                  <div class="form-group">
                    <label for="exampleInputPassword1">Parcel Price</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Parcel Price" v-model="parcelprice" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Maximum Price</label>                    
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Maximum Price" v-model="donkeyprice" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Display Price</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Display Price" v-model="offerprice" required>
                  </div>
                  
                </div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'ParcelRateadd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {}, id: '', selectedparcel: '', selectedsize: 'S-Small', selectedfrom: '', selectedto: '', parcelprice: '', donkeyprice: '', offerprice: '', listParcel: [], listCity: []
    }
  },
  created: function () { this.autoInc() },
  methods: {
    autoInc () {
      let uris = this.$hostName + '/common/CityMaster'
      this.axios.get(uris).then((response) => {
        this.listCity = response.data
      })
      uris = this.$hostName + '/parcel/ParcelCompany'
      this.axios.get(uris).then((response) => {
        this.listParcel = response.data
      })

      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/parcel/ParcelRate/Auto'
        this.axios.get(uri).then((response) => {
          console.log(response.data[0])
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/parcel/ParcelRate/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
                    // this.item = response.data;
          this.id = response.data[0]['id']
          this.item.id = this.id
          this.selectedparcel = response.data[0]['ParcelName']
          this.selectedsize = response.data[0]['ParcelSize']
          this.selectedfrom = response.data[0]['FromCity']
          this.selectedto = response.data[0]['ToCity']
          this.parcelprice = response.data[0]['parcelprice']
          this.donkeyprice = response.data[0]['donkeyprice']
          this.offerprice = response.data[0]['offerprice']
        })
      }
    },
    addItem () {
      let uri = this.$hostName + '/parcel/ParcelRate/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/parcel/ParcelRate/update' }
      this.item.id = this.id
      this.item.selectedparcel = this.selectedparcel
      this.item.selectedsize = this.selectedsize
      this.item.selectedfrom = this.selectedfrom
      this.item.selectedto = this.selectedto
      this.item.parcelprice = this.parcelprice
      this.item.donkeyprice = this.donkeyprice
      this.item.offerprice = this.offerprice

      this.axios.post(uri, this.item).then((response) => { this.$router.push({name: 'ParcelRatevw'}) })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



