<template>
 <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Service City
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Service City</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->
<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add Service City</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Service City</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Service City" v-model="servicearea" v-autofocus required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Latitude</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Latitude" v-model="lati" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Longitude</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Longitude" v-model="long" required>
                  </div>
                  <div class="form-group row">
                    <div class="col-md-12"><label for="exampleInputPassword1">Choose Services</label></div>
                    <div class="col-md-3"><input type="checkbox"  value="Courier" v-model="ccourier">&nbsp;Courier</div>
                    <div class="col-md-3"><input type="checkbox" class="form-check-input" value="Parcel" v-model="cparcel">&nbsp;Parcel</div>                    
                    <div class="col-md-3"><input type="checkbox"  value="Courier" v-model="csharing">&nbsp;Sharing</div>
                    <div class="col-md-3"><input type="checkbox" class="form-check-input" value="Parcel" v-model="crental">&nbsp;Rental</div>                    
                  </div>
                </div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'ServiceAreaadd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {}, id: '', servicearea: '', long: '', lati: ''
    }
  },
  created: function () { this.autoInc() },
  methods: {
    autoInc () {
      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/common/ServiceArea/Auto'
        this.axios.get(uri).then((response) => {
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/common/ServiceArea/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
                    // this.item = response.data;
          this.id = response.data[0]['id']
          this.item.id = this.id
          this.servicearea = response.data[0]['ServiceArea']
          this.long = response.data[0]['Longitude']
          this.lati = response.data[0]['Latitude']
          this.ccourier = false
          this.cparcel = false
          this.csharing = false
          this.crental = false
          if (response.data[0]['isCourier'] === 'true') { this.ccourier = true }
          if (response.data[0]['isParcel'] === 'true') { this.cparcel = true }
          if (response.data[0]['isSharing'] === 'true') { this.csharing = true }
          if (response.data[0]['isRental'] === 'true') { this.crental = true }
        })
      }
      this.$refs.search.focus()
    },
    addItem () {
      let uri = this.$hostName + '/common/ServiceArea/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/common/ServiceArea/update' }
      this.item.id = this.id
      this.item.servicearea = this.servicearea
      this.item.long = this.long
      this.item.lati = this.lati
      var ccourier1 = 'false'
      var cparcel1 = 'false'
      var csharing1 = 'false'
      var crental1 = 'false'
      if (this.ccourier === true) ccourier1 = 'true'
      if (this.cparcel === true) cparcel1 = 'true'
      if (this.csharing === true) csharing1 = 'true'
      if (this.crental === true) crental1 = 'true'
      this.item.ccourier = ccourier1
      this.item.cparcel = cparcel1
      this.item.csharing = csharing1
      this.item.crental = crental1
      this.axios.post(uri, this.item).then((response) => { this.$router.push({name: 'ServiceAreavw'}) })
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



