<template>
 <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Daily Discount
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Daily Discount</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add DailyDiscount</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Day</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedday" required v-autofocus>
                    <option value="">Select Day</option>
                    <option v-for="dept in days" v-bind:value="dept.Day">
                      {{dept.Day}}
                      </option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Service Area</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedSarea" required>
                    <option value="">Select ServiceArea</option>
                    <option v-for="dept in listServicearea" v-bind:value="dept.ServiceArea">
                      {{dept.ServiceArea}}
                      </option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Service Type</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedType" required>
                    <option value="">Select ServiceType</option>
                    <option v-for="dept in listType" v-bind:value="dept.Type">
                      {{dept.Type}}
                      </option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <!-- (<i class="fa fa-inr" aria-hidden="true"></i>) -->
                    <label for="exampleInputPassword1">Discount % </label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Discount Perc" v-model="disc" required>
                  </div>
                  <div class="form-group row">
                    <div class="col-md-4"><label for="exampleInputPassword1">Active Offer</label></div>
                    <div class="col-md-3"><input type="radio" name="act" class="form-check-input" value="Yes" v-model="ActiveY">Yes</div>
                    <div class="col-md-3"><input type="radio" name="act" class="form-check-input" value="No" v-model="ActiveN">No </div>                    
                  </div>
                </div>

               <!-- <div class="form-group">
                    <label for="exampleInputPassword1">File Upload</label>
                    <input type="file" class="form-control" id="exampleInputPassword1" @change="imageChanged">
                </div>-->
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'DailyDiscountadd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {},
      id: '',
      product: {
        image: ''
      },
      disc: '',
      desc: '',
      days: [
      { id: 1, Day: 'Monday' },
      { id: 2, Day: 'Tuesday' },
      { id: 3, Day: 'Wednesday' },
      { id: 4, Day: 'Thursday' },
      { id: 5, Day: 'Friday' },
      { id: 6, Day: 'Saturday' },
      { id: 7, Day: 'Sunday' }
      ],
      listServicearea: [],
      selectedday: '',
      selectedSarea: '',
      selectedType: '',
      listType: [
      {Type: 'Courier'},
      {Type: 'Parcel'},
      {Type: 'Sharing'},
      {Type: 'Rental'}
      ],
      ActiveY: '',
      ActiveN: ''
    }
  },
  created: function () { this.autoInc() },
  methods: {
    imageChanged (e) {
      console.log(e.target.files[0])
      var fileReader = new FileReader()

      fileReader.readAsDataURL(e.target.files[0])
      fileReader.onload = (e) => {
        this.product.image = e.target.result
      }
      console.log(this.product)
    },
    autoInc () {
      let uris = this.$hostName + '/common/ServiceArea'
      this.axios.get(uris).then((response) => {
        this.listServicearea = response.data
      })

      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/common/DailyDiscount/Auto'
        this.axios.get(uri).then((response) => {
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/common/DailyDiscount/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
                    // this.item = response.data;
          this.id = response.data[0]['id']
          this.selectedday = response.data[0]['Day']
          this.selectedSarea = response.data[0]['ServiceArea']
          this.selectedType = response.data[0]['ServiceType']
          this.disc = response.data[0]['Discount']
          if (response.data[0]['isActive'] === 1) { this.ActiveY = 'Yes' } else { this.ActiveN = 'No' }
        })
      }
      this.$refs.search.focus()
    },
    addItem () {
      let uri = this.$hostName + '/common/DailyDiscount/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/common/DailyDiscount/update' }
      this.item.id = this.id
      this.item.selectedday = this.selectedday
      this.item.selectedSarea = this.selectedSarea
      this.item.selectedType = this.selectedType
      this.item.disc = this.disc
      this.item.image = this.product.image
      this.item.isactive = this.ActiveY === 'Yes' ? '1' : '0'
      this.axios.post(uri, this.item).then((response) => { this.$router.push({name: 'DailyDiscountvw'}) })
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



