<template>
   <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Courier Link
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Courier Link</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->
<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add Courier Link</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Courier Name</label>                    
                    <select class="form-control select2" style="width: 100%;" v-model="couriername" value="Select Courier" required v-autofocus>
                      <option value="">Select Courier</option>
                      <option v-for="dept in listCourier" v-bind:value="dept.CourierName">
                      {{dept.CourierName}}
                      </option>
                    </select>                    
                  </div>                   
                  <div class="form-group">
                    <label for="exampleInputPassword1">State</label>                    
                    <select class="form-control select2" style="width: 100%;" v-model="fromState" @change="onChange" required>
                      <option value="">Select State</option>
                      <option  v-for="dept in listState" v-bind:value="dept.State">
                      {{dept.State}}
                      </option>
                    </select>                    
                  </div>                  
                   <div class="form-group">
                    <label for="exampleInputPassword1">City</label>                    
                    <select class="form-control select2" style="width: 100%;" v-model="fromCity" required>
                      <option value="">Select City</option>
                      <option  v-for="dept in listCity" v-bind:value="dept.City">
                      {{dept.City}}
                      </option>
                    </select>                    
                  </div>                  
                  <div class="form-group">
                    <label for="exampleInputPassword1">Area</label>                    
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Area" v-model="fromArea" required>
                  </div>
                  <!-- <div class="form-group">
                    <label for="exampleInputPassword1">City</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter City" v-model="city" required>
                  </div> -->
                  <div class="form-group">
                    <label for="exampleInputPassword1">Latitude</label>                    
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Latitude" v-model="latitude" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Longitude</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Longitude" v-model="longitude" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Description</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Description" v-model="desc">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1"></label>
                    <div><input type="checkbox" class="form-check-input" value="Courier" v-model="cblock">Block this Location</div>
                  </div>
                </div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'CourierLinkadd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {}, id: '', couriername: '', selectedType: 'Document', len: '', wid: '', perkg: '', fromState: '', fromArea: '', fromCity: '', latitude: '', longitude: '', offerprice: '', listCourier: [], listState: [], document1: true, box1: false, desc: '', listCity: [], cblock: ''
    }
  },

  created: function () { this.autoInc() },
  methods: {
    onChange: function () {
      let uris = this.$hostName + '/common/CityMaster/Search'
      let ResMsg = { }
      ResMsg.state = this.fromState
      this.axios.post(uris, ResMsg).then((response) => {
        this.listCity = response.data
        console.log(response)
      })
    },
    autoInc () {
      let uris = this.$hostName + '/common/StateMaster'
      this.axios.get(uris).then((response) => {
        this.listState = response.data
      })

      let uric = this.$hostName + '/courier/CourierCompany'
      this.axios.get(uric).then((response) => {
        this.listCourier = response.data
      })

      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/courier/CourierLink/Auto'
        this.axios.get(uri).then((response) => {
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/courier/CourierLink/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
                    // this.item = response.data;
          this.id = response.data[0]['id']
          this.couriername = response.data[0]['CourierName']
          this.fromState = response.data[0]['State']
          this.fromCity = response.data[0]['City']
          this.fromArea = response.data[0]['Area']
          this.latitude = response.data[0]['Latitude']
          this.longitude = response.data[0]['Longitude']
          this.desc = response.data[0]['Description']
          this.cblock = false
          if (response.data[0]['isBlock'] === 'true') { this.cblock = true }
          this.onChange()
        })
      }
    },
    addItem () {
      let uri = this.$hostName + '/courier/CourierLink/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/courier/CourierLink/update' }
      this.item.id = this.id
      this.item.couriername = this.couriername
      this.item.fromState = this.fromState
      this.item.fromCity = this.fromCity
      this.item.fromArea = this.fromArea
      this.item.latitude = this.latitude
      this.item.longitude = this.longitude
      this.item.desc = this.desc
      var cblock1 = 'false'
      if (this.cblock === true) cblock1 = 'true'
      this.item.cblock = cblock1
      this.axios.post(uri, this.item).then((response) => { this.$router.push({name: 'CourierLinkvw'}) })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



