<template>
<div class="Admin-view">

   <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Message Template
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Message Template</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add Message Template</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="row">
                  <div class="col-md-4">
                    <label for="exampleInputPassword1">Message Code</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Type Msg Code" v-model="mcode" v-autofocus required>
                  </div>
                  <div class="col-md-8">
                    <label for="exampleInputPassword1">Message Name</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Type Message Name" v-model="mname" required>
                  </div>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Message Template</label>
                    <textarea class="form-control" id="exampleInputPassword1" placeholder="Type Here..." v-model="note" required></textarea>
                  </div>
                </div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'MessageTemplateadd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {}, id: '', mcode: '', mname: '', note: ''
    }
  },
  created: function () { this.autoInc() },
  methods: {
    autoInc () {
      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/advanced/MessageTemplate/Auto'
        this.axios.get(uri).then((response) => {
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/advanced/MessageTemplate/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
                    // this.item = response.data;
          this.id = response.data[0]['id']
          this.item.id = this.id
          this.mcode = response.data[0]['MessageCode']
          this.mname = response.data[0]['MessageName']
          this.note = response.data[0]['MessageTemplate']
        })
      }
      this.$refs.search.focus()
    },
    addItem () {
      let uri = this.$hostName + '/advanced/MessageTemplate/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/advanced/MessageTemplate/update' }
      this.item.id = this.id
      this.item.mcode = this.mcode
      this.item.mname = this.mname
      this.item.note = this.note
      this.axios.post(uri, this.item).then((response) => { this.$router.push({name: 'MessageTemplatevw'}) })
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



