<template>
    <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Parcel Company
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Parcel Company</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add Parcel</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Parcel Name</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Parcel Name" v-model="parcelname" v-autofocus ref="focus" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">City</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedcity" required>
                    <option value="">Select City</option>
                    <option v-for="dept in listCity" v-bind:value="dept.City">
                      {{dept.City}}
                      </option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Latitude</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Latitude" v-model="lati" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Longitude</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Longitude" v-model="long" required>
                  </div>
                  
                </div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'ParcelCompanyadd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {}, id: '', parcelname: '', listCity: [], lati: '', long: '', selectedcity: ''
    }
  },
  created: function () { this.autoInc() },
  methods: {
    autoInc () {
      let uris = this.$hostName + '/common/CityMaster'
      this.axios.get(uris).then((response) => {
        this.listCity = response.data
      })

      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/parcel/ParcelCompany/Auto'
        this.axios.get(uri).then((response) => {
          console.log(response.data[0])
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/parcel/ParcelCompany/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
                    // this.item = response.data;
          this.id = response.data[0]['id']
          this.item.id = this.id
          this.parcelname = response.data[0]['ParcelName']
          this.selectedcity = response.data[0]['City']
          this.lati = response.data[0]['Latitude']
          this.long = response.data[0]['Longitude']
        })
      }
    },
    addItem () {
      let uri = this.$hostName + '/parcel/ParcelCompany/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/parcel/ParcelCompany/update' }
      this.item.id = this.id
      this.item.parcelname = this.parcelname
      this.item.selectedcity = this.selectedcity
      this.item.lati = this.lati
      this.item.long = this.long

      this.axios.post(uri, this.item).then((response) => { this.$router.push({name: 'ParcelCompanyvw'}) })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



