<template>
  <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Vehicle Category Master
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Vehicle Category</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

      <!-- Content Body-->

      <div class="col-md-12">
        <div class="card">
          <div class="card-header">
            <div class="row">
                  <div class="col-sm-6"><h4 class="text-left">Search : <input type="text" name="" v-model="search" placeholder="Type Here..."></h4></div>
                  <div class="col-sm-6"><h3 class="card-title text-right"><router-link :to="{ name: 'CategoryMasteradd' }" class="btn btn-primary">Add Vehicle Category</router-link></h3></div>
              </div>
          </div>
          <!-- /.card-header -->
          <div class="card-body p-0">
            <table class="table table-condensed">
              <tr>
                <th style="width: 5%">#</th>
                <th style="width: 45%">Category Name</th>
                <th style="width: 40%">Description</th>
                <th style="width: 5%;">Action</th>
              </tr>
              <tbody>
                <tr v-for="(item,index) in filteredCustomers">
                  <td>{{ item.id }}</td>
                  <td>{{ item.Category }}</td>
                  <td>{{ item.Description }}</td>
                  <td>
                    <router-link :to="{name: 'CategoryMasteradd',params:{id: item.id}}" class="btn btn-primary">Edit</router-link>
                  </td>
                  <td>
                    <button class="btn btn-danger" v-on:click="deleteItem(item.id,index)">Delete</button>
                  </td>
                </tr>
              </tbody>

            </table>
          </div>
          <!-- /.card-body -->
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  export default {
    name: 'CategoryMastervw',
    data () { return {items: [], search: ''} },
    computed:
    {
      filteredCustomers: function () {
        var self = this
        return this.items.filter(function (cust) { return cust.Category.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 })
     // return this.customers;
      }
    },
    created: function () {
      this.FetchItem()
    },
    methods: { FetchItem () {
      let uri = this.$hostName + '/common/CategoryMaster'
      this.axios.get(uri).then((response) => {
        this.items = response.data
      })
    },
      deleteItem (id1, ri) {
        var r = confirm('Are u want to Delete?')
        if (r === true) {
          let uri = this.$hostName + '/common/CategoryMaster/Del/' + id1
          this.items.splice(ri, 1)
          this.axios.get(uri).then((response) => {
  // console.log(response.data);
            this.$router.push({name: 'CategoryMastervw'})
          })
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped="">

</style>



