import ParcelCompanyadd from '../components/parcel/ParcelCompanyadd'
import ParcelCompanyvw from '../components/parcel/ParcelCompanyvw'
import ParcelRateadd from '../components/parcel/ParcelRateadd'
import ParcelRatevw from '../components/parcel/ParcelRatevw'
import HiringRateadd from '../components/parcel/HiringRateadd'
import HiringRatevw from '../components/parcel/HiringRatevw'
// import ParcelConfig from '../components/parcel/ParcelConfig'

const ParcelRoutes = [
  {
    path: '/parcel/ParcelCompany',
    name: 'ParcelCompanyvw',
    component: ParcelCompanyvw
  },
  {
    path: '/parcel/ParcelCompany/add',
    name: 'ParcelCompanyadd',
    component: ParcelCompanyadd
  },
  {
    path: '/parcel/ParcelRate',
    name: 'ParcelRatevw',
    component: ParcelRatevw
  },
  {
    path: '/parcel/ParcelRate/add',
    name: 'ParcelRateadd',
    component: ParcelRateadd
  },
  {
    path: '/parcel/HiringRate/add',
    name: 'HiringRateadd',
    component: HiringRateadd
  },
  {
    path: '/parcel/HiringRate',
    name: 'HiringRatevw',
    component: HiringRatevw
  }
]

export default ParcelRoutes



