<template>
    <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Hiring Rate
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Hiring Rate</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add Hiring Rate</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Vehicle Category</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedcategory" v-autofocus required>
                    <option value="">Select Category</option>
                    <option v-for="dept in listCategory" v-bind:value="dept.Category">
                      {{dept.Category}}
                      </option>
                    </select>                    
                  </div>
                   <div class="form-group">
                    <label for="exampleInputPassword1">Service City</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedcity" required>
                    <option value="">Select Service City</option>
                    <option v-for="dept in listCity" v-bind:value="dept.ServiceArea">
                      {{dept.ServiceArea}}
                      </option>
                    </select>                    
                  </div>
                   <div class="row">
                      <div class="col-md-4">
                    <label for="exampleInputEmail1">Hiring Price<span><h6>(for 4KM)</h6></span></label>
                    <span><input type="text" class="form-control" id="exampleInputEmail1" v-model="sminRate" required></span></div>
                 <div class="col-md-4">
                    <label for="exampleInputEmail1">Maximum Price<span><h6>(for 4KM)</h6></span></label>
                    <span><input type="text" class="form-control" id="exampleInputEmail1" v-model="dminRate" required></span></div>
                    <div class="col-md-4">
                    <label for="exampleInputEmail1">Display Price<span><h6>(for 4KM)</h6></span></label>
                    <span><input type="text" class="form-control" id="exampleInputEmail1" v-model="tminRate"></span></div>
                    <div class="col-md-4">
                    <label for="exampleInputEmail1">Additional Price<span><h6>(for Every 1KM)</h6></span></label>
                    <span><input type="text" class="form-control" id="exampleInputEmail1" v-model="saddRate" required></span></div>
                    <div class="col-md-4">
                    <label for="exampleInputEmail1">Maximum Price<span><h6>(for Every 1KM)</h6></span></label>
                    <span><input type="text" class="form-control" id="exampleInputEmail1" v-model="daddRate" required></span></div>
                    <div class="col-md-4">
                    <label for="exampleInputEmail1">Display Price<span><h6>(for Every 1KM)</h6></span></label>
                    <span><input type="text" class="form-control" id="exampleInputEmail1" v-model="taddRate"></span></div>
                  </div>
                  
                </div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'HiringRateadd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {}, id: '', selectedcity: '', sminRate: '', saddRate: '', dminRate: '', daddRate: '', listCity: [], selectedcategory: '', listCategory: [], tminRate: '', taddRate: ''
    }
  },
  created: function () { this.autoInc() },
  methods: {
    autoInc () {
      let uris = this.$hostName + '/common/ServiceArea'
      this.axios.get(uris).then((response) => {
        this.listCity = response.data
      })
      uris = this.$hostName + '/common/CategoryMaster'
      this.axios.get(uris).then((response) => {
        this.listCategory = response.data
      })

      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/parcel/HiringRate/Auto'
        this.axios.get(uri).then((response) => {
          console.log(response.data[0])
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/parcel/HiringRate/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
                    // this.item = response.data;
          this.id = response.data[0]['id']
          this.item.id = this.id
          this.selectedcategory = response.data[0]['Category']
          this.selectedcity = response.data[0]['ServiceCity']
          this.sminRate = response.data[0]['driverMinRate']
          this.saddRate = response.data[0]['driverAddlRate']
          this.dminRate = response.data[0]['DMinRate']
          this.daddRate = response.data[0]['DAddlRate']
          this.tminRate = response.data[0]['tMinRate']
          this.taddRate = response.data[0]['tAddlRate']
        })
      }
    },
    addItem () {
      let uri = this.$hostName + '/parcel/HiringRate/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/parcel/HiringRate/update' }
      this.item.id = this.id
      this.item.selectedcategory = this.selectedcategory
      this.item.selectedcity = this.selectedcity
      this.item.sminRate = this.sminRate
      this.item.saddRate = this.saddRate
      this.item.dminRate = this.dminRate
      this.item.daddRate = this.daddRate
      this.item.tminRate = this.tminRate
      this.item.taddRate = this.taddRate

      this.axios.post(uri, this.item).then((response) => { this.$router.push({name: 'HiringRatevw'}) })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



