<template>
  <!-- Content Wrapper. Contains page content -->
  <div id="content-wrap" class="content-wrapper">
    <!-- Content Header (Page header) -->

   <!-- admin  
    <section class="content-header" v-show="admin">
      <h1>
        Admin Dashboard
      </h1>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">{{breadcrumlink}}</li>
      </ol>
    </section>
     end admin-->


    <!-- courier 
     <section class="content-header" v-show="courier">
      <h1>
        Courier Dashboard
      </h1>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Dashboard</li>
      </ol>
    </section>
     end couriers -->
    <!-- User -->
     <section class="content-header" v-show="user">
      <h1>
        Welcome Username
      </h1>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Dashboard</li>
      </ol>
    </section>
    <section class="content">
      <transition name="page" mode="out-in">
        <router-view></router-view>
      </transition>
    </section>
    <!-- /.content -->
  </div>
  <!-- /.content-wrapper -->
</template>

<script>
export default {
  name: 'va-content-wrap',
  data () {
    courier = false
    user = false
    admin = false
    breadcrumlink = String
  },
  created () {
    let url = window.location.pathname
    let arrres = url.split('/')
    let res = '/' + arrres[1]
    this.breadcrumlink = arrres[2]
    if (res === '/courier') {
      this.courier = true
    } else if (res === '/admin') {
      this.admin = true
    } else if (res === '/user') {
      this.user = true
    }
  }
}
</script>

<style>
.page-enter-active, .page-leave-active {
  transition: opacity 0.5s, transform 0.5s;
}
.page-enter, .page-leave-to {
  opacity: 0;
}
</style>



