import AdminDashboard from '../examples/Dashboard.v1.vue'
import ProductTypevw from '../components/admin/ProductTypevw.vue'
import ProductTypeadd from '../components/admin/ProductTypeadd.vue'
import StateMastervw from '../components/admin/StateMastervw.vue'
import StateMasteradd from '../components/admin/StateMasteradd.vue'
import CityMastervw from '../components/admin/CityMastervw.vue'
import CityMasteradd from '../components/admin/CityMasteradd.vue'
import ServiceAreavw from '../components/admin/ServiceAreavw.vue'
import ServiceAreaadd from '../components/admin/ServiceAreaadd.vue'
import DailyDiscountvw from '../components/admin/DailyDiscountvw.vue'
import DailyDiscountadd from '../components/admin/DailyDiscountadd.vue'
import VehicleTypeadd from '../components/admin/VehicleTypeadd'
import VehicleTypevw from '../components/admin/VehicleTypevw'
import CategoryMasteradd from '../components/admin/CategoryMasteradd'
import CategoryMastervw from '../components/admin/CategoryMastervw'
const AdminRoutes = [
  {
    path: '/admin',
    name: 'Dashboard',
    component: AdminDashboard
  },
  {
    path: '/admin/productType',
    name: 'ProductTypevw',
    component: ProductTypevw
  },
  {
    path: '/admin/productType/add',
    name: 'ProductTypeadd',
    component: ProductTypeadd
  },
  {
    path: '/admin/StateMaster',
    name: 'StateMastervw',
    component: StateMastervw
  },
  {
    path: '/admin/StateMaster/add',
    name: 'StateMasteradd',
    component: StateMasteradd
  },
  {
    path: '/admin/CityMaster',
    name: 'CityMastervw',
    component: CityMastervw
  },
  {
    path: '/admin/CityMaster/add',
    name: 'CityMasteradd',
    component: CityMasteradd
  },
  {
    path: '/admin/ServiceArea',
    name: 'ServiceAreavw',
    component: ServiceAreavw
  },
  {
    path: '/admin/ServiceArea/add',
    name: 'ServiceAreaadd',
    component: ServiceAreaadd
  },
  {
    path: '/admin/DailyDiscount',
    name: 'DailyDiscountvw',
    component: DailyDiscountvw
  },
  {
    path: '/admin/DailyDiscount/add',
    name: 'DailyDiscountadd',
    component: DailyDiscountadd
  },
  {
    path: '/admin/VehicleCategory/add',
    name: 'CategoryMasteradd',
    component: CategoryMasteradd
  },
  {
    path: '/admin/VehicleCategory',
    name: 'CategoryMastervw',
    component: CategoryMastervw
  },
  {
    path: '/admin/VehicleType/add',
    name: 'VehicleTypeadd',
    component: VehicleTypeadd
  },
  {
    path: '/admin/VehicleType',
    name: 'VehicleTypevw',
    component: VehicleTypevw
  }
]

export default AdminRoutes



