// import CourierDashboard from '../components/courier/Dashboard.vue'
import CourierCompanyvw from '../components/courier/CourierCompanyvw'
import CourierCompanyadd from '../components/courier/CourierCompanyadd'
import CourierRatevw from '../components/courier/CourierRatevw'
import CourierRateadd from '../components/courier/CourierRateadd'
import CourierLinkvw from '../components/courier/CourierLinkvw'
import CourierLinkadd from '../components/courier/CourierLinkadd'
import CourierConfig from '../components/courier/CourierConfig'

const CourierRoutes = [
  {
    path: '/courier/CourierCompany',
    name: 'CourierCompanyvw',
    component: CourierCompanyvw
  },
  {
    path: '/courier/CourierCompany/add',
    name: 'CourierCompanyadd',
    component: CourierCompanyadd
  },
  {
    path: '/courier/CourierRate',
    name: 'CourierRatevw',
    component: CourierRatevw
  },
  {
    path: '/courier/CourierRate/add',
    name: 'CourierRateadd',
    component: CourierRateadd
  },
  {
    path: '/courier/CourierLink',
    name: 'CourierLinkvw',
    component: CourierLinkvw
  },
  {
    path: '/courier/CourierLink/add',
    name: 'CourierLinkadd',
    component: CourierLinkadd
  },
  {
    path: '/courier/CourierConfig',
    name: 'CourierConfig',
    component: CourierConfig
  }
]

export default CourierRoutes



