<template>
 <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Service Area
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Service Area</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->
<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add Service Area</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Service City</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedcity" v-autofocus required>
                    <option value="">Select Service City</option>
                    <option v-for="dept in listCity" v-bind:value="dept.ServiceArea">
                      {{dept.ServiceArea}}
                      </option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Service Area</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Service Area" v-model="servicearea" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Latitude</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Latitude" v-model="lati" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Longitude</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Longitude" v-model="long" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Radius</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Radius" v-model="radius" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Notes</label>
                    <textarea class="form-control" id="exampleInputPassword1" placeholder="Type Here..." v-model="note" required></textarea>
                    <!--<input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Radius" v-model="radius" required>-->
                  </div>
                </div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'ServiceArea1add',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {}, id: '', selectedcity: '', servicearea: '', long: '', lati: '', listCity: [], radius: '', note: ''
    }
  },
  created: function () { this.autoInc() },
  methods: {
    autoInc () {
      let uris = this.$hostName + '/common/ServiceArea'
      this.axios.get(uris).then((response) => {
        this.listCity = response.data
      })
      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/sharing/ServiceArea/Auto'
        this.axios.get(uri).then((response) => {
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/sharing/ServiceArea/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
                    // this.item = response.data;
          this.id = response.data[0]['id']
          this.item.id = this.id
          this.selectedcity = response.data[0]['ServiceCity']
          this.servicearea = response.data[0]['ServiceArea']
          this.long = response.data[0]['Longitude']
          this.lati = response.data[0]['Latitude']
          this.radius = response.data[0]['Radius']
          this.note = response.data[0]['Notes']
        })
      }
      this.$refs.search.focus()
    },
    addItem () {
      let uri = this.$hostName + '/sharing/ServiceArea/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/sharing/ServiceArea/update' }
      this.item.id = this.id
      this.item.selectedcity = this.selectedcity
      this.item.servicearea = this.servicearea
      this.item.long = this.long
      this.item.lati = this.lati
      this.item.radius = this.radius
      this.item.note = this.note
      this.axios.post(uri, this.item).then((response) => { this.$router.push({name: 'ServiceArea1vw'}) })
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



