import SharingRateadd from '../components/sharing/SharingRateadd'
import SharingRatevw from '../components/sharing/SharingRatevw'
import ServiceArea1add from '../components/sharing/ServiceArea1add'
import ServiceArea1vw from '../components/sharing/ServiceArea1vw'

const SharingRoutes = [
  {
    path: '/sharing/SharingRate/add',
    name: 'SharingRateadd',
    component: SharingRateadd
  },
  {
    path: '/sharing/SharingRate',
    name: 'SharingRatevw',
    component: SharingRatevw
  },
  {
    path: '/sharing/ServiceArea',
    name: 'ServiceArea1vw',
    component: ServiceArea1vw
  },
  {
    path: '/sharing/ServiceArea/add',
    name: 'ServiceArea1add',
    component: ServiceArea1add
  }
]

export default SharingRoutes



