<template>
    <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Sharing Rate
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Sharing Rate</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add Sharing Rate</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Service City</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedcity" v-autofocus required>
                    <option value="">Select Service City</option>
                    <option v-for="dept in listCity" v-bind:value="dept.ServiceArea">
                      {{dept.ServiceArea}}
                      </option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Sharing Size</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedsize">
                    <option selected="selected" value="S-Small">S-Small</option>
                    <option value="M-Medium">M-Medium</option>
                    <option value="L-Large">L-Large</option>
                    <option value="XL-ExtraLarge">XL-ExtraLarge</option>
                    </select>                    
                  </div>
                  <div class="row">                  
                  <div class="col-md-4">
                    <label for="exampleInputPassword1">Sharing Price<span><h5>(for 4KM)</h5></span></label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Sharing Price" v-model="sharingprice" required>
                  </div>
                  <div class="col-md-4">
                    <label for="exampleInputPassword1">Maximum Price<span><h5>(for 4KM)</h5></span></label>                    
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Max Price" v-model="donkeyprice" required>
                  </div>
                  <div class="col-md-4">
                    <label for="exampleInputPassword1">Display Price<span><h5>(for 4KM)</h5></span></label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Display Price" v-model="offerprice" required>
                  </div>
                  </div>
                  <div class="row">                  
                  <div class="col-md-4">
                    <label for="exampleInputPassword1">Additional Price<span><h5>(for Every 1KM)</h5></span></label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Addl Price" v-model="SAddlRate" required>
                  </div>
                  <div class="col-md-4">
                    <label for="exampleInputPassword1">Maximum Price<span><h5>(for Every 1KM)</h5></span></label>                    
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Max Price" v-model="SMaxRate" required>
                  </div>
                  <div class="col-md-4">
                    <label for="exampleInputPassword1">Display Price<span><h5>(for Every 1KM)</h5></span></label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Display Price" v-model="SDispRate" required>
                  </div>
                  </div>
                </div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'SharingRateadd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {}, id: '', selectedcity: '', selectedsize: 'S-Small', sharingprice: '', donkeyprice: '', offerprice: '', listCity: [], SAddlRate: '', SMaxRate: '', SDispRate: ''
    }
  },
  created: function () { this.autoInc() },
  methods: {
    autoInc () {
      let uris = this.$hostName + '/common/ServiceArea'
      this.axios.get(uris).then((response) => {
        this.listCity = response.data
      })

      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/sharing/SharingRate/Auto'
        this.axios.get(uri).then((response) => {
          console.log(response.data[0])
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/sharing/SharingRate/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
                    // this.item = response.data;
          this.id = response.data[0]['id']
          this.item.id = this.id
          this.selectedcity = response.data[0]['ServiceArea']
          this.selectedsize = response.data[0]['SharingSize']
          this.sharingprice = response.data[0]['sharingprice']
          this.donkeyprice = response.data[0]['donkeyprice']
          this.offerprice = response.data[0]['offerprice']
          this.SAddlRate = response.data[0]['SAddlRate']
          this.SMaxRate = response.data[0]['SMaxRate']
          this.SDispRate = response.data[0]['SDispRate']
        })
      }
    },
    addItem () {
      let uri = this.$hostName + '/sharing/SharingRate/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/sharing/SharingRate/update' }
      this.item.id = this.id
      this.item.selectedcity = this.selectedcity
      this.item.selectedsize = this.selectedsize
      this.item.sharingprice = this.sharingprice
      this.item.donkeyprice = this.donkeyprice
      this.item.offerprice = this.offerprice
      this.item.SAddlRate = this.SAddlRate
      this.item.SMaxRate = this.SMaxRate
      this.item.SDispRate = this.SDispRate
      this.axios.post(uri, this.item).then((response) => { this.$router.push({name: 'SharingRatevw'}) })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



