<template>
<div class="Admin-view">

   <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Product Master
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Product Type</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add Product</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Product Name</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Product Name" v-model="cname" v-autofocus required>
                  </div>
                  <div class="form-group row">
                    <div class="col-md-4"><label for="exampleInputPassword1">Choose Type</label></div>
                    <div class="col-md-3"><input type="checkbox" class="form-check-input" value="Courier" v-model="ccourier">Courier</div>
                    <div class="col-md-3"><input type="checkbox" class="form-check-input" value="Parcel" v-model="cparcel">Parcel </div>                    
                  </div>
                </div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'ProductTypeadd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {}, id: '', cname: '', ccourier: '', cparcel: ''
    }
  },
  created: function () { this.autoInc() },
  methods: {
    autoInc () {
      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/common/ProductType/Auto'
        this.axios.get(uri).then((response) => {
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/common/ProductType/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
                    // this.item = response.data;
          this.id = response.data[0]['id']
          this.item.id = this.id
          this.cname = response.data[0]['ProductType']
          this.ccourier = false
          this.cparcel = false
          if (response.data[0]['isCourier'] === 'true') { this.ccourier = true }
          if (response.data[0]['isParcel'] === 'true') { this.cparcel = true }
        })
      }
    },
    addItem () {
      let uri = this.$hostName + '/common/ProductType/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/common/ProductType/update' }
      this.item.id = this.id
      this.item.cname = this.cname
      var ccourier1 = 'false'
      var cparcel1 = 'false'
      if (this.ccourier === true) ccourier1 = 'true'
      if (this.cparcel === true) cparcel1 = 'true'
      this.item.ccourier = ccourier1
      this.item.cparcel = cparcel1
      this.axios.post(uri, this.item).then((response) => { this.$router.push({name: 'ProductTypevw'}) })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



