<template>
<div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        City Master
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">City Master</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->
<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add City</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                 <div class="form-group">
                    <label for="exampleInputPassword1">State</label>                    
                    <select v-model="state" class="form-control select2" style="width: 100%;" v-autofocus required>
                      <option  v-for="dept in listState" v-bind:value="dept.State">
                      {{dept.State}}
                      </option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">City Name</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter City Name" v-model="cname" ref="search" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Description</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Description" v-model="desc" >
                  </div>
                </div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'CityMasteradd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {}, id: '', cname: '', state: '', desc: '', listState: []
    }
  },
  created: function () { this.autoInc() },
  methods: {
    autoInc () {
      let uri = this.$hostName + '/common/StateMaster'
      this.axios.get(uri).then((response) => {
        this.listState = response.data
      })

      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/common/CityMaster/Auto'
        this.axios.get(uri).then((response) => {
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/common/CityMaster/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
                    // this.item = response.data;
          this.id = response.data[0]['id']
          this.item.id = this.id
          this.cname = response.data[0]['City']
          this.state = response.data[0]['State']
          this.desc = response.data[0]['Description']
        })
      }
    },
    addItem () {
      let uri = this.$hostName + '/common/CityMaster/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/common/CityMaster/update' }
      this.item.id = this.id
      this.item.cname = this.cname
      this.item.state = this.state
      this.item.desc = this.desc
      console.log(this.item)
      this.axios.post(uri, this.item).then((response) => { this.$router.push({name: 'CityMastervw'}) })
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



