<template>
<div class="Admin-view">

   <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Share Pass
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Share Pass</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add Share Pass</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Service City</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedcity" v-autofocus required>
                    <option value="">Select Service City</option>
                    <option v-for="dept in listCity" v-bind:value="dept.ServiceArea">
                      {{dept.ServiceArea}}
                      </option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">KMs</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedkms" required>
                    <option value="">Select KMs</option>
                    <option value="4KM">4KM</option>
                    <option value="8KM">8KM</option>
                    <option value="12KM">12KM</option>
                    </select>                    
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Size</label>
                    <select class="form-control select2" style="width: 100%;" v-model="selectedsize">
                    <option value="">Select Size</option>
                    <option value="S-Small">S-Small</option>
                    <option value="M-Medium">M-Medium</option>
                    <option value="L-Large">L-Large</option>
                    <option value="XL-ExtraLarge">XL-ExtraLarge</option>
                    </select>                    
                  </div>
                  <div class="row">
                  <div class="col-md-6">
                    <label for="exampleInputEmail1">No of Rides</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" v-model="rides">
                    </div>
                  <div class="col-md-6">
                    <label for="exampleInputEmail1">No of Days</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" v-model="days">
                  </div>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputEmail1">Maximum Price</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" v-model="maxprice">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputEmail1">Display Price</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" v-model="disprice">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputEmail1">Flat Price per Ride</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" v-model="flat">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputEmail1">Offer %</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" v-model="offerp">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Description</label>
                    <textarea class="form-control" id="exampleInputPassword1" placeholder="Type Here..." v-model="desc" ></textarea>
                  </div>
                    <div class="form-group row">
                      <div class="col-md-6">
                    <label for="exampleInputPassword1">Image Upload</label>
                    <input type="file" accept="image/*" class="form-control" id="exampleInputPassword1" @change="imageChanged">
                    <div class="col-md-12" style="padding-top: 1em">
                    <progress :value.prop="uploadPercentage" max="100" v-if="isProgress"></progress>
                    </div>
                    </div>
                    <div class="col-md-6">                    
                    <img :src="product.image" width="230" height="180">
                  </div>
                </div>
                </div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'SharePassaddadd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {}, id: '', listCity: [], selectedcity: '', selectedkms: '', selectedsize: '', rides: '', days: '', maxprice: '', disprice: '', flat: '', offerp: '', desc: '', product: { image: '' }, uploadPercentage: 0, isProgress: false
    }
  },
  created: function () { this.autoInc() },
  methods: {
    imageChanged (e) {
      console.log(e.target.files[0])
      this.product.files = e.target.files[0]
      var fileReader = new FileReader()

      fileReader.readAsDataURL(e.target.files[0])
      fileReader.onload = (e) => {
        this.product.image = e.target.result
      }
    },
    autoInc () {
      let uris = this.$hostName + '/common/ServiceArea'
      this.axios.get(uris).then((response) => {
        this.listCity = response.data
      })
      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/advanced/SharePass/Auto'
        this.axios.get(uri).then((response) => {
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/advanced/SharePass/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
          this.id = response.data[0]['id']
          this.item.id = this.id
          this.selectedcity = response.data[0]['ServiceCity']
          this.selectedkms = response.data[0]['KMs']
          this.selectedsize = response.data[0]['Size']
          this.rides = response.data[0]['Rides']
          this.days = response.data[0]['Days']
          this.maxprice = response.data[0]['MaxPrice']
          this.disprice = response.data[0]['DispPrice']
          this.flat = response.data[0]['FlatPrice']
          this.offerp = response.data[0]['OfferP']
          this.desc = response.data[0]['Description']
          // this.product.image = response.data[0]['Picture']
          this.product.image = response.data[0]['filePath']
          imageChanged()
        })
      }
      this.$refs.search.focus()
    },
    addItem () {
      this.isProgress = true
      let data = new FormData()
      let file = this.product.files
      data.append('file', file)
      let uri = this.$hostName + '/advanced/SharePass/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/advanced/SharePass/update' }
      // this.item.id = this.id
      // this.item.selectedcity = this.selectedcity
      // this.item.selectedkms = this.selectedkms
      // this.item.selectedsize = this.selectedsize
      // this.item.rides = this.rides
      // this.item.days = this.days
      // this.item.maxprice = this.maxprice
      // this.item.disprice = this.disprice
      // this.item.flat = this.flat
      // this.item.offerp = this.offerp
      // this.item.desc = this.desc
      // this.item.image = this.product.image
      data.append('id', this.id)
      data.append('selectedcity', this.selectedcity)
      data.append('selectedkms', this.selectedkms)
      data.append('selectedsize', this.selectedsize)
      data.append('rides', this.rides)
      data.append('days', this.days)
      data.append('maxprice', this.maxprice)
      data.append('disprice', this.disprice)
      data.append('flat', this.flat)
      data.append('offerp', this.offerp)
      data.append('desc', this.desc)
      this.axios.post(uri, data, {onUploadProgress: function (progressEvent) {
        this.uploadPercentage = parseInt(Math.round((progressEvent.loaded * 100) / progressEvent.total))
      }.bind(this) }).then((response) => { this.$router.push({name: 'SharePassvw'}) })
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



